<template>
    <div class="admin-login">
        <div class="admin-title">
            <p class="sub-title">網站管理者</p>
            <p class="title">Admin System</p>
        </div>
        <div class="admin-content">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>管理者系統</el-breadcrumb-item>
            </el-breadcrumb>
        </div>   
        <div class="admin-login">
            <div class="input">
                <el-input v-model="Username" prefix-icon="el-icon-user-solid" suffix-icon="el-icon-question" placeholder="Username"></el-input>
                <el-input v-model="Password" prefix-icon="el-icon-lock"  placeholder="Password" show-password></el-input>
                <div class="forgot-box">
                    <el-checkbox v-model="checked">記住帳號密碼</el-checkbox>
                    <router-link to="/forgetAdmin" class="pwd">忘記密碼</router-link>
                </div>
                <div class="login-btn" @click="goAdmin">確認登入</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'adminLogin',
    data() {
        return {
            checked: false,
            Username: '',
            Password: '',

        }
    },
    methods: {
        goAdmin() {
            this.$router.push(`/admin/leads`)
        }
    }

}
</script>

<style lang='scss' scoped>
.admin-login {
    margin: 105px 0;
    .admin-title {
        background: url('~@/assets/img/admin/admin-banner.png') no-repeat center;
        background-size: cover;
    }
    .admin-content {
        max-width: 1200px;
        width: 80%;
        margin: 50px auto;
    }
        
    .admin-login {
        margin: 100px 0;
        padding: 100px 0;
        background: #f4f4f4;
        .input {
            max-width: 400px;
            margin: auto;
            width: 80%;
            div {
                margin-bottom: 30px;
            }
        }
        .forgot-box {
            font-size: 14px;
            padding: 0 25px;
            display: flex;
            justify-content: space-between;
            align-items: baseline;
            margin-bottom: 50PX;
            .pwd {
                text-decoration: underline;
            }
        }
        .login-btn {
            width: 100%;
            line-height: 70px;
            border-radius: 20px;
            text-align: center;
            background: #ef6d38;
            color: #fff;
            font-size: 20px;
            cursor: pointer;
            transition: all .3s;
            position: relative;
            overflow: hidden;
            &:hover {
                filter: brightness(130%)
            }
        }
    }
}


</style>


